#root{
  margin: 0 12rem;
  background-color: rgba(180, 12, 158, 0.836);
  border-radius:0.5rem;
}
.title{
  color: white;
  text-align: center;
  text-decoration: underline;
  padding:1rem;
}
.app{
  text-align: center;
}
.row{
  display: flex;
  justify-content: space-evenly;
  width: 200px;
  &-container{
    display: flex;
    justify-content:center;
    padding: 0.5rem;
  }
  padding: 0.3rem;
}
.time{
  height:100px;
  width: 100px;
  text-align: center;
  padding: 4rem;
  margin: 2rem;
  display: flex;
  justify-content: center;
  align-content: center;
  flex-direction: column;
  border: 1px solid white;
  color:white;
  border-radius: 50%;
  &-container{
    display: flex;
    justify-content:center;
  }
}
.modal{
  position: fixed; /* Stay in place */
  z-index: 1; /* Sit on top */
  left: 0;
  top: 0;
  width: 100%; /* Full width */
  height: 100%; /* Full height */
  background-color: white;
  overflow: auto;
  opacity: 1;
  margin: 0.5rem 0;
  &-title{
    text-align: center;
  }
  &-container{
    margin: 0 12rem;
    background-color: rgba(180, 12, 158, 0.836);
    border-radius:0.5rem;
  }
  &-title{
    color: white;
    text-align: center;
    text-decoration: underline;
    padding:2rem;
  }
}
.close,.close-and-play{
  margin: 1rem;
  color: #aaa;
  float: right;
  font-size: 28px;
  font-weight: bold;
  &:hover,&:focus{
  color: black;
  text-decoration: none;
  cursor: pointer;
  }
}
.break{
  text-align: center;
}

//TOGGLE
.toggle-switch {
  position: relative;
  display: inline-block;
  width: 50px;
  height: 25px;
  margin: 0.5rem 0.5rem;
}
.toggle-switch input[type="checkbox"] {
  display: none;
}
.toggle-switch .switch {
  position: absolute;
  cursor: pointer;
  background-color: #ccc;
  border-radius: 25px;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  transition: background-color 0.2s ease;
}
.toggle-switch .switch::before {
  position: absolute;
  content: "";
  left: 2px;
  top: 2px;
  width: 21px;
  height: 21px;
  background-color: #aaa;
  border-radius: 50%;
  transition: transform 0.3s ease;
}
.toggle-switch input[type="checkbox"]:checked + .switch::before {
  transform: translateX(25px);
  background-color: rgba(41, 1, 35, 0.836);
}
.toggle-switch input[type="checkbox"]:checked + .switch {
  background-color: rgba(41, 1, 35, 0.836);
}